.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.image-wrapper {
		position: relative;
		width: 90%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		background: $stroke-gradient;
		padding: 1px;
		border-radius: 50%;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			background: var(--color-main);
			padding: 2rem;
			border-radius: 50%;
			overflow: hidden;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 50%;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 2rem 2rem 3rem;
			@include max-md {
				padding: 1rem 1rem 2rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
