.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		color: $color-custom;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 5rem 0 3rem;
		}
	}
	.couple-body {
		position: relative;
		padding: 2rem 1rem;
		border-radius: var(--border-radius);
		overflow: hidden;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 3/4;
			margin: 0 auto;
			border-radius: 50%;
			background: $stroke-gradient;
			padding: 1px;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 2rem;
				border-radius: 50%;
				background: var(--color-main);
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 50%;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
			color: $color-custom;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			margin: 0 auto;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
